@mixin flexCenter($direction, $style, $justify, $align) {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: $justify;
  align-items: $align;
  list-style-type: $style;
  flex-direction: $direction;
  flex-wrap: wrap;
}

$background: rgba(255, 255, 255, 0.3);

.col-md-3 {
  padding: 0 !important;
  margin: 0 !important;
}

.col-md-9 {
  padding: 0 !important;
  margin: 0 !important;
}

.row {
  padding: 0 !important;
  margin: 0 !important;
}

.myPhoto {
  width: 100%;
  height: 100%;
}

.view-container {
  @include flexCenter(row, none, center, center);
  background: url("./images/back.jpg") no-repeat center center fixed;
  min-height: 100vh;
  height: inherit;
}

.content-container {
  padding: 1em 0;
  width: 100%;
  min-height: 100%;
}

.description-container {
  @include flexCenter(row, none, none, none);
}

.header-container {
  background: $background;

  .header-text-container {
    @include flexCenter(row, none, center, center);
    min-height: 100%;
    padding: 0 3em;
  }
}

.name-container {
  background: rgba(68, 64, 64, 0.8);
  h1 {
    color: white;
    font-family: emoji;
    font-size: 28px;
    text-transform: uppercase;
    padding: 0 0.3em;
    margin: 0;
  }
}

.personal-container {
  background: $background;
  padding: 0 1em;
  min-height: 100%;
  h4 {
    @include flexCenter(column, none, center, center);
  }
  ul {
    @include flexCenter(column, none, flex-start, flex-start);
    padding: 0.2em 0;
    li {
      margin: -1px;
    }
  }
}

.property-circle-container {
  h5 {
    font-size: 18px;
  }
  ul {
    @include flexCenter(row, none, space-between, flex-start);
    li {
      @include flexCenter(row, none, center, center);
    }
  }
}

.main-content-background-container {
  background: white;
  // min-height: 100%;
}

.main-content-container {
  padding: 0.2em 0;
  h4 {
    @include flexCenter(row, none, center, center);
  }

  .footer-border {
    padding: 0.2em 0;
    border-top: 2px solid rgb(214, 209, 209);
    border-radius: 2px;
  }
  .footer-container {
    @include flexCenter(row,none ,center,flex-start );
    p {
      font-size: 14px;
      padding: 3px;
    }

    a {
      color: black;
      text-decoration: none;
      padding: 0 0.2em;

      &:hover {
        color: black;
        text-decoration: none;
      }
    }
  }

  .main-content-border {
    padding: 0.2em 0;
    border-bottom: 2px solid rgb(214, 209, 209);
    border-radius: 2px;
  }

  .experience-container {
    padding: 0.3em 0 0 0;
    margin-bottom: -15px;

    .experience-company-name-container {
      @include flexCenter(column, none, center, center);
      min-height: 100%;

      p {
        padding: 1em 0 0 0;
        font-size: 10px;
        color: rgba(0, 0, 0, 0.5);
        text-align: center;
      }
    }
    h4 {
      @include flexCenter(row, none, center, flex-end);
    }

    a {
      color: black;
      text-decoration: none;

      &:hover {
        color: black;
        text-decoration: none;
      }
    }
  }

  .main-content-properties-container {
    padding: 0.5em 0;
  }
}

.circle-container {
  padding: 0;
  margin: 0;
  @include flexCenter(row, none, center, center);
  ul {
    @include flexCenter(row, none, center, center);
  }
}

.circle {
  width: 12px;
  height: 12px;
}

.circle-padding {
  padding: 0 0.1em;
  margin-top: -5px;
}

.footer-container {
  background-color: white;
  // @include flexCenter(row, none, center, flex-start);

  .footer-border {
    padding: 0.2em 0;
    border-top: 2px solid rgb(214, 209, 209);
    border-radius: 2px;
  }

  p {
    font-size: 14px;
    padding: 2px;
  }

  a {
    color: black;
    text-decoration: none;
    padding: 0 0.2em;

    &:hover {
      color: black;
      text-decoration: none;
    }
  }
}

@media only screen and (max-width: 770px) {
  .view-container {
    background: url("./images/back.jpg") no-repeat center center;
    background-size: cover;

    .circle {
      width: 8px;
      height: 8px;
    }
    .property-circle-container {
      h6 {
        font-size: 12px;
      }
      ul {
        @include flexCenter(row, none, space-between, flex-start);
        li {
          @include flexCenter(row, none, center, center);
        }
      }
    }

    .footer-container {
      @include flexCenter(row, none, center, flex-start);
      p {
        font-size: 10px;
        padding: 0px;
      }

      a {
        color: black;
        text-decoration: none;
        padding: 0 0.2em;
        font-size: 10px;

        &:hover {
          color: black;
          text-decoration: none;
        }
      }
    }
  }

  .name-container {
    @include flexCenter(row, none, center, center);
  }

  .header-container {
    .header-text-container {
      padding: 1em;
      text-align: center;
    }
  }
}

@media only screen and (min-width: 770px) and (max-width: 1000px) {
  .name-container {
    @include flexCenter(row, none, center, center);
  }
}
